import * as React from 'react';
import TextField from '@mui/material/TextField';
import FormControl from '@mui/material/FormControl';
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import imgEnigma1 from '../assets/images/enigma-1-final.png';
import imgEnigma2 from '../assets/images/enigma-2-final.png';
import imgEnigma3 from '../assets/images/enigma-3-final.png';
import imgEnigma4 from '../assets/images/enigma-4-final.png';
import imgEnigma5 from '../assets/images/enigma-5-final.png';
import imgEnigma6 from '../assets/images/enigma-6-final.png';
import imgEnigma7 from '../assets/images/enigma-7-final.png';
import Box from '@mui/material/Box';
import * as HindingPlaces from './HidingPlaces'

export default function EnigmaForm(props) {
  const [response, setResponse] = React.useState('');
  const [password, setPassword] = React.useState('');

  const enigmasList = [
    {
      id: 1,
      clue: `Vous dcouvrirez le premier de ces indices dans ${HindingPlaces.place1}`,
      password: 'matamou',
      description: 'Le premier indice que nous cherchons est entour par quatre oiseaux (au-dessus, au-dessous,  sa droite et  sa gauche). Le voyez-vous ?',
      srcImg: imgEnigma1,
      response: 'arbre'
    },
    {
      id: 3,
      clue: `Vous dcouvrirez le deuxime indice dans ${HindingPlaces.place2}`,
      password: 'alunira',
      description: 'Observez bien l\'expression du visage des deux graines magiques parfaitement identiques. Ce deuxime indice vous servira tout  lheure pour deviner dans quel village danimaux se trouve le livre sacr.',
      srcImg: imgEnigma2,
      response: 'grimace'
    },
    {
      id: 4,
      clue: `Vous dcouvrirez le troisime indice dans ${HindingPlaces.place3}`,
      password: 'jungle',
      description: 'Quel fruit doit remplacer le point dinterrogation dans cette srie ? Il sagit de votre troisime et dernier indice.',
      srcImg: imgEnigma3,
      response: 'banane'
    },
    {
      id: 5,
      clue: `Vous dcouvrirez le quatrime indice dans ${HindingPlaces.place4}`,
      password: 'hibou',
      description: 'Les Singes sont terroriss ! Des animaux les ont forcs  voler le livre, mais ils ont trop peur de nous rvler leur nom. Ces crapules sont reparties avec le livre sacr il y a tout juste dix minutes. Nous avons montr aux Singes la photo de quatre animaux que nous pensons capables dun tel acte. Ils nous ont rpondu que le coupable figurait bien parmi ces quatre animaux et quil ne ressemblait pas  un cheval. Cela devrait dj vous permettre dinnocenter, puis de rayer, un de ces suspects. Continuez ensuite votre enqute pour dcouvrir plus dinformations sur les odieux animaux qui sont en possession du livre.',
      srcImg: imgEnigma4,
      response: 'zbre'
    },
    {
      id: 7,
      clue: `Vous dcouvrirez le cinquime indice dans ${HindingPlaces.place5}`,
      password: 'livre',
      description: 'Voici un nouvel animal  rayer sur votre feuille. Il vient dtre pris en photo depuis lendroit o il passe ses vacances. Il na donc pas pu voler le livre sacr. Le voyez-vous ?',
      srcImg: imgEnigma5,
      response: 'perroquet'
    },
    {
      id: 8,
      clue: `Vous dcouvrirez le sixime indice dans ${HindingPlaces.place6}`,
      password: 'savane',
      description: 'Les loups ont peur de leau et les rhinocros ont peur des serpents. Celui qui na pas pu accder au village des Singes est innocent, tandis que lautre est le coupable de votre histoire.  vous de jouer !',
      srcImg: imgEnigma6,
      response: 'loup'
    },
    {
      id: 10,
      clue: `Vous dcouvrirez le septime indice dans ${HindingPlaces.place7}`,
      password: 'magie',
      description: 'Nous avons retrouv la trace de la meute des loups. Tentons de les endormir en leur lanant des flchettes de somnifres. Nous pourrons ainsi rcuprer le livre. Levez un feutre au-dessus de la tte, choisissez un loup puis fermez les yeux. Descendez tout doucement le feutre jusqu ce quil touche la feuille. Ouvrez les yeux puis observez si vous avez touch un loup. Passez ensuite le feutre  un coquipier. Quand tous les loups ont t touchs, vous avez russi la mission.',
      srcImg: imgEnigma7,
      response: 'trsor'
    },
  ];
  const [animate, setAnimate] = React.useState(false);
  const [showEnigma, setShowEnigma] = React.useState(false)

  const activeEnigma = enigmasList.find((element) => {
    return element.id === props.activeStep;
  })

  const verifyPassword = () => {
    if (password === activeEnigma.password) {
      setShowEnigma(true);
    }
  }

  const handleChangePassword = (event) => {
    setPassword(event.target.value);
  };

  const handleChange = (event) => {
    setResponse(event.target.value);
  };

  const handleSubmit = (event) => {
    setAnimate(false);

    if (response.toLowerCase() === activeEnigma.response) {
      props.handleNext();
    } else {
      setAnimate(true);
    }

    event.preventDefault();
  }

  return (
    <React.Fragment>
      {showEnigma ? (
        <Grid
          spacing={3}
          container
          direction="column"
          justifyContent="center"
        >
          <Grid item>
            <Box sx={{textAlign: 'center', fontSize: 18}}>
              {activeEnigma.description}
            </Box>
          </Grid>
          <Grid item>
            <form onSubmit={handleSubmit}>
              <Grid
                spacing={3}
                container
                direction="row"
                justifyContent="center"
                alignItems="center"
              >
                <Grid item>
                  <FormControl variant="standard">
                    <TextField 
                      label="rponse"
                      color="secondary"
                      focused
                      value={response}
                      onChange={handleChange}
                      className={"" + (animate ? 'animate__animated animate__shakeX' : '')}
                    />
                  </FormControl>
                </Grid>
                <Grid item>
                  <FormControl variant="standard">
                    <Button variant="contained" size="large" type='submit'>
                      Rpondre
                    </Button>
                  </FormControl>          
                </Grid>
              </Grid>
            </form> 
          </Grid>
          <Grid item>
            <div className='img-wrapper'>
              <img src={activeEnigma.srcImg} alt='enigma'></img>
            </div>
          </Grid>
        </Grid>
      ):(
        <React.Fragment>
          <p style={{fontSize: 18}}>{activeEnigma.clue}</p>
          <Grid
            spacing={3}
            container
            direction="row"
            justifyContent="center"
            alignItems="center"
          >
            <Grid item>
              <FormControl variant="standard">
                <TextField 
                  label="Mot de passe"
                  color="secondary"
                  focused
                  value={password}
                  onChange={handleChangePassword}
                />
              </FormControl>
            </Grid>
            <Grid item>
              <FormControl variant="standard">
                <Button variant="contained" size="large" onClick={verifyPassword}>
                  Dbloquer
                </Button>
              </FormControl>          
            </Grid>
          </Grid>
        </React.Fragment>
      )}
    </React.Fragment>
  );
}